<style>
</style>

<script>
import { useQuasar } from "quasar";
import { ref } from "vue";
import Header from "../../components/Header.vue";

const setup = {
  origin: process.env.VUE_APP_URL_API_ORIGIN,
  url: process.env.VUE_APP_URL_API_COLLABORATORS,
  urluser: process.env.VUE_APP_URL_API_USERS

};

export default {
  name: "Home",
  search: "Pesquisar",
  components: {
    Header,
  },
  setup() {
    const $q = useQuasar();
    const submitting = ref(false);

    return {
      setup: setup,
      submitting,
      pageTitle: "Editar: ",
      pageSubTitle: "Dados de acesso",
      showNotif(msg, color, icon) {
        $q.notify({
          message: msg,
          color: color,
          icon: icon,
        });
      },
    };
  },
  data() {
    return {
      transaction: null,
      id: null,
      uid: null,
      username: null,
      name: null,
      email: null,
      password: null,
      role: null,
      type: null,
      crmv: null,
      phone: null,
      avatar: null,
      status: null,
      expertise: null
    };
  },
  methods: {
    async getCollaborator() {
      const req = await fetch(this.setup.url +'/'+ this.$route.params.id);
      this.data = await req.json();

      this.user = this.data.data.user;
      this.collaborator = this.data.data.collaborator;

      (this.uid = this.user.id),
      (this.username = this.user.username),
      (this.type = this.user.type),
      (this.status = this.user.status),
      (this.id = this.collaborator.id),    
      (this.name = this.collaborator.name),   
      (this.email = this.collaborator.email),      
      (this.role = this.collaborator.role),
      (this.crmv = this.collaborator.crmv),
      (this.phone = this.collaborator.phone),
      (this.avatar = this.collaborator.avatar),
      (this.expertise = this.collaborator.expertise)
      
    },
    async editCollaborator() {
      this.submitting = true;

      const data = {
        transaction: 'update',
        id: this.id,
        uid: this.uid,
        name: this.name,
        email: this.email,
        crmv: this.crmv,
        avatar: this.avatar,
        phone: this.phone,
        expertise: this.expertise,
        role: this.role       

      };

      const dataJson = JSON.stringify(data);

      if (data.name && data.email && data.phone) {
        const req = await fetch(this.setup.url +'/'+ this.id + '/edit', {
          method: "POST",
          mode: 'cors',
          headers: {
            'Origin': this.setup.origin,
            'Accept': 'application/json, text/javascript, */*; q=0.01',
            'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
          },  
          body: dataJson,
        });

        const res = await req.json();

        if (res) {
          this.transaction = "",
          this.id = "";
          this.uid = "";
          this.name = "";
          this.email = "";
          this.password = "";
          this.crmv = "";
          this.avatar = "";
          this.phone = "";
          this.expertise = "";
          this.role = "";           
 
          this.showNotif(
            res.data.msg,
            "green",
            "las la-thumbs-up"
          );
          this.$router.go(-1);
        } else {
          this.showNotif(
            "Ooops! Algo inesperado ocorreu. Tente mais tarde!",
            "red",
            "las la-bomb"
          );
        }
      } else {
        this.showNotif(
          "Por favor. Os campos com * são obrigatórios.",
          "red",
          "las la-exclamation-triangle"
        );
      }
      this.submitting = false;
    },
    async editUser() {
      
      this.submitting = true;

      const data = {
        transaction: 'update',
        id: this.uid,
        username: this.username,
        password: this.password,
        status: this.status,
        type: this.type      

      };

      const dataJson = JSON.stringify(data);

       if (data.id) {
        const req = await fetch(this.setup.urluser +'/'+ this.uid + '/edit', {
          method: "POST",
          mode: 'cors',
          headers: {
            'Origin': this.setup.origin,
            'Accept': 'application/json, text/javascript, */*; q=0.01',
            'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
          },  
          body: dataJson,
        });

        const res = await req.json();

        if (res) {
          this.transaction = "",
          this.id = "";
          this.username = "";
          this.password = "";
          this.status = "";
          this.type = "";   
   
          this.showNotif(
            res.data.msg,
            "green",
            "las la-thumbs-up"
          );

          this.$router.go(-1);

        } else {

          this.showNotif(
            "Ooops! Algo inesperado ocorreu. Tente mais tarde!",
            "red",
            "las la-bomb"
          );

        }

       } else {

        this.showNotif(
          "Por favor. Os campos com * são obrigatórios.",
          "red",
          "las la-exclamation-triangle"
        );

       }      

      this.submitting = false;
        
      }
  },

  mounted() {
    this.getCollaborator();
  },
};
</script>
